.container {
    margin: 50px 0px;
  }
  
  .card {
    margin: 0 2% !important;
    
  }
  
   .infected {
    background-color: rgba(241, 3, 3, 0.411) !important;
    
    border-bottom: 10px solid rgba(0, 0, 255, 0.5);
  }
  
  .recovered {
    background-color: rgba(14, 209, 56, 0.5) !important;
    border-bottom: 10px solid rgba(255, 196, 0, 0.452);
  }
  
  .deaths {
    background-color: rgba(237, 178, 178, 0.527) !important;
    border-bottom: 10px solid rgba(255, 0, 0, 0.5);
  }
  
  .active {
    background-color: rgba(152, 227, 237, 0.5) !important;
    border-bottom: 10px solid rgba(242, 234, 0, 0.5);
  }
   
  @media (max-width: 770px) {
    .card {
      margin: 2% 0 !important;
    }
  }